import React from 'react';
import { withRouter } from 'react-router-dom';

const CardWithBack = props => {
  const { pageTitle, rightTopSection, dataLoadeed, errorMsg, children } = props;

  if (pageTitle === undefined || pageTitle === null || pageTitle === '') {
    throw new Error('pageTitle is required params');
  }
  if (dataLoadeed === undefined || dataLoadeed === null || dataLoadeed === '') {
    throw new Error('dataLoadeed is required params with type boolean');
  }
  if (children === undefined || children === null || children === '') {
    throw new Error('children is required params');
  }

  return (
    <div className="wrapper wrapper-content ecommerce" id="user-details">
      <div className="row">
        <div className="col-lg-12">
          <div className="ibox-content">
            <div className="box-heading">
              <div className="row ">
                <div className="col-lg-6 col-md-12 col-sm-12 col-xs-12">
                  <h2>
                    <button
                      type="button"
                      className="cardwithback-btn"
                      onClick={() => props.history.goBack()}
                    >
                      <img
                        src={require('../../assets/img/back-icon.png')}
                        className="img-back"
                        alt=""
                      />
                    </button>
                    {pageTitle}
                  </h2>
                </div>
                {dataLoadeed && rightTopSection}
              </div>
              {dataLoadeed ? (
                children
              ) : (
                <div className="error-card-div">
                  {errorMsg !== undefined && errorMsg !== ''
                    ? errorMsg
                    : 'Loading...'}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(CardWithBack);
