import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
// import CKEditor from 'ckeditor4-react';
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import {
  getCMSByIdSaga,
  updateCMSSaga,
  resetCMS,
} from '../../../store/actions';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

// eslint-disable-next-line no-unused-vars
const EditCMS = props => {
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const [isDisable, setIsDisable] = useState(false);
  const [contentFromEditor, setContent] = useState('');
  // const [isEditable, setEditable] = useState(false);
  const { CMS, errorMsg } = useSelector(state => state.cms);
  const { open } = useSelector(state => state.modal);

  const dispatch = useDispatch();

  useEffect(() => {
    // props
    const { key } = props.match.params;
    setMounted(true);
    dispatch(getCMSByIdSaga({ id: key }));
    return () => {
      setMounted(false);
      dispatch(resetCMS());
    };
  }, []);

  const handleEdit = () => {
    setIsDisable(true);
    dispatch(
      updateCMSSaga({
        title: CMS.title,
        content: contentFromEditor || CMS.content,
        setIsDisable,
      }),
    );
  };

  const handleContentChange = (event, editor) => {
    const data = editor.getData();
    setContent(data);
    // if(!isEditable){
    //   setEditable(true);
    // }
  };

  return (
    <CardWithBack
      pageTitle="Edit CMS Page"
      dataLoadeed={CMS._id !== undefined}
      errorMsg={errorMsg}
    >
      <div className="row margin-b-15 ck-editor-dev">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="row margin-b-15">
            <div className="col-lg-6 col-md-12 col-sm-12 col-xs-12">
              <h4>Title</h4>
              <input
                type="text"
                className="form-control"
                value={CMS.title}
                disabled
                placeholder="Write the Page title Here"
              />
            </div>
          </div>
          <div className="row">
            <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <h4>Content</h4>
              <div className="App">
                <CKEditor
                  editor={ClassicEditor}
                  data={CMS.content}
                  config={{
                    removePlugins: [
                      'MediaEmbed',
                      'ImageUpload',
                      'ImageToolbar',
                      'ImageCaption',
                      'ImageStyle',
                    ],
                    image: {},
                  }}
                  onChange={(event, editor) =>
                    handleContentChange(event, editor)
                  }
                  // config={{
                  //   toolbar: [
                  //     {
                  //       name: 'clipboard',
                  //       groups: ['clipboard', 'undo'],
                  //       items: [
                  //         'Cut',
                  //         'Copy',
                  //         'Paste',
                  //         'PasteText',
                  //         'PasteFromWord',
                  //         '-',
                  //         'Undo',
                  //         'Redo',
                  //       ],
                  //     },
                  //     '/',
                  //     {
                  //       name: 'basicstyles',
                  //       groups: ['basicstyles', 'cleanup'],
                  //       items: [
                  //         'Bold',
                  //         'Italic',
                  //         'Underline',
                  //         'Strike',
                  //         'Subscript',
                  //         'Superscript',
                  //         '-',
                  //         'RemoveFormat',
                  //       ],
                  //     },
                  //     {
                  //       name: 'paragraph',
                  //       groups: ['list', 'indent', 'blocks', 'align', 'bidi'],
                  //       items: [
                  //         'NumberedList',
                  //         'BulletedList',
                  //         '-',
                  //         'Outdent',
                  //         'Indent',
                  //         '-',
                  //         'Blockquote',
                  //         'CreateDiv',
                  //         '-',
                  //         'JustifyLeft',
                  //         'JustifyCenter',
                  //         'JustifyRight',
                  //         'JustifyBlock',
                  //         '-',
                  //         'BidiLtr',
                  //         'BidiRtl',
                  //         'Language',
                  //       ],
                  //     },
                  //     { name: 'links', items: ['Link', 'Unlink'] },
                  //     {
                  //       name: 'insert',
                  //       items: [
                  //         'Image',
                  //         'Flash',
                  //         'Table',
                  //         'HorizontalRule',
                  //         'Smiley',
                  //         'SpecialChar',
                  //         'PageBreak',
                  //         'Iframe',
                  //       ],
                  //     },
                  //     '/',
                  //     {
                  //       name: 'styles',
                  //       items: ['Styles', 'Format', 'Font', 'FontSize'],
                  //     },
                  //     { name: 'colors', items: ['TextColor', 'BGColor'] },
                  //   ],
                  // }}

                  // // eslint-disable-next-line react/jsx-no-duplicate-props
                  // onChange={event => {
                  //   const data = event.editor.getData();
                  //   handleContentChange(data);
                  // }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="">
        <div className="text-center">
          <button
            type="submit"
            className="btn btn-primary"
            style={{ marginRight: '10px' }}
            onClick={e => handleEdit(e)}
            disabled={isDisable}
          >
            Edit
          </button>
          <button
            type="button"
            disabled={isDisable}
            className="btn btn-primary"
            onClick={() => props.history.goBack()}
          >
            Cancel
          </button>
        </div>
      </div>
      {open && <AlertMessageModal />}
    </CardWithBack>
  );
};

export default withRouter(SetTokenHeader(EditCMS, axios));
